<template>
  <div id="headerbar">
    <h1>信工党建e家</h1>
    <el-breadcrumb separator="/" class="mid">
      <el-breadcrumb-item v-for="(item,index) in routerList" :to="item.path" :key="index" class="router">{{item.name}}
      </el-breadcrumb-item>
    </el-breadcrumb>
    <div class="right">
      <el-dropdown>
        <img :src="data.img" alt="">
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item @click.native="loginOut">退出登录</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>

    </div>
  </div>
</template>

<script>
  export default {
    name: "headerbar",
    data() {
      return {
        data: {},
        routerList:[]
      }
    },
    methods: {
      loginOut() {
        this.$axios.post('loginOut').then(res => {
          if (res.code == 200) {
            this.$message({
              message: '退出登录成功',
              type: 'success'
            });
            this.$router.push('/login')
          } else {
            this.$message.error('退出登录失败');
          }
        })
      },
      getMsg() {
        this.$axios.post('getUser').then(res => {
          if (res.code == 200) {
            this.data = res.data[0];
          } else {
            this.$message.error('退出登录失败');
          }
        })
      },
      getRouter() {
        this.routerList = this.$route.matched.filter(item => item.name)
      }
    },
    watch: {
      $route() {
        this.getRouter();
      }
    },
    created() {
      this.getMsg();
      this.getRouter()
    }
  }
</script>

<style scoped>
  .router{
    display: block;
    line-height: 60px;
    color: #fff;
  }
  .mid{
    display: inline-block;
    margin-left: 180px;
    margin-right: 60px;
   line-height: 100%;
    font-size: 20px;
  }
  #headerbar {
    position: fixed;
    height: 60px;
    width: 100%;
    z-index: 998;
    background: #20a1ff;
    color: #fff;
  }

  #headerbar h1 {
    line-height: 60px;
    padding-left: 20px;
    float: left;
    font-size: 18px;
  }

  .right {
    float: right;
  }

  #headerbar img {
    display:inline-block;
    padding: 10px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
  }
</style>

<style>
  #headerbar .el-breadcrumb__inner,.el-breadcrumb__separator{
    color: #fff;
  }
</style>




